.about-me-section{flex-direction:column;max-width:900px;align-items:center;margin-inline:auto}.about-me-section p ul{max-width:500px;margin-inline:auto}.about-me-section p ul:not(p:last-child){margin-bottom:10px}.about-me-section img{width:-webkit-fill-available;height:-webkit-fill-available;width:-moz-available;height:-moz-available;width:fill-available;height:fill-available}.about-me-section .inner{display:grid;grid-template-columns:3fr 2fr;gap:50px}.about-me-section ul.skills-list{display:grid;grid-template-columns:repeat(2, minmax(140px, 200px));gap:8px 10px;margin:20px 0px 0px;overflow:hidden;list-style:none;font-size:15px;justify-content:start}.about-me-section ul.skills-list li::before{content:"â–¹";color:var(--light-green);position:relative;left:0;width:1rem;line-height:12px;padding-right:7px}.about-me-section .numbered-heading{display:flex;-webkit-box-align:center;align-items:center;position:relative;margin:10px 0px 40px;font-family:SansBold;max-width:900px;align-content:center;font-size:clamp(26px,5vw,30px);white-space:nowrap;color:var(--lightest-slate)}.about-me-section .numbered-heading::before{content:"01.";height:100%;position:relative;top:4px;margin-right:10px;color:var(--light-green);font-size:clamp(7px,3.2vw,18px);font-weight:400;font-family:"mono"}.about-me-section .numbered-heading::after{content:"";display:block;height:1px;width:250px;background:var(--slate);margin:1rem 0 .8rem 1rem;opacity:.3}.about-right .wrapper .img-container img{border-radius:4px}.about-right{position:relative;max-width:300px;max-height:265px;margin-inline:auto}.about-right .wrapper{display:block;border-radius:4px;max-width:500px;background-color:var(--light-green)}.about-right .wrapper::before,.about-right .wrapper::after{content:"";display:block;position:absolute;width:100%;height:100%;border-radius:3px}.about-right .wrapper::after{border:2px solid var(--light-green);top:20px;left:20px;z-index:-1;border-radius:4px;transition:all 250ms ease}.about-right .wrapper:hover .img-container{filter:none;mix-blend-mode:unset;transition:all 250ms ease}.about-right .wrapper:hover::after{transition:all 250ms ease;transform:translate(-5px, -5px)}.about-right .wrapper .img-container{mix-blend-mode:multiply;filter:grayscale(100%) contrast(1);border-radius:4px;transition:all 250ms ease}@media only screen and (max-width: 950px){.inner{display:block !important}.wrapper{margin-top:70px}.numbered-heading{justify-content:center;width:100% !important}.numbered-heading::after{width:100% !important}}